import type { NestedKeyOf } from "@jsondraw/common/utility-types";
import { useAtomValue, editorJotaiStore, atom } from "./editor-jotai";
import fallbackLangData from "./locales/en.json";
import percentages from "./locales/percentages.json";

const COMPLETION_THRESHOLD = 85;
const EXTRA_LANGUAGE_CODES = new Set(["vi-VN"]);

export interface Language {
  code: string;
  label: string;
  rtl?: boolean;
}

export type TranslationKeys = NestedKeyOf<typeof fallbackLangData>;

export const defaultLang = { code: "en", label: "English" };

export const languages: Language[] = [
  defaultLang,
  ...[
    { code: "ar-SA", label: "", rtl: true },
    { code: "bg-BG", label: "" },
    { code: "ca-ES", label: "Catal" },
    { code: "cs-CZ", label: "esky" },
    { code: "de-DE", label: "Deutsch" },
    { code: "el-GR", label: "" },
    { code: "es-ES", label: "Espaol" },
    { code: "eu-ES", label: "Euskara" },
    { code: "fa-IR", label: "", rtl: true },
    { code: "fi-FI", label: "Suomi" },
    { code: "fr-FR", label: "Franais" },
    { code: "gl-ES", label: "Galego" },
    { code: "he-IL", label: "", rtl: true },
    { code: "hi-IN", label: "" },
    { code: "hu-HU", label: "Magyar" },
    { code: "id-ID", label: "Bahasa Indonesia" },
    { code: "it-IT", label: "Italiano" },
    { code: "ja-JP", label: "" },
    { code: "kab-KAB", label: "Taqbaylit" },
    { code: "kk-KZ", label: " " },
    { code: "ko-KR", label: "" },
    { code: "ku-TR", label: "Kurd" },
    { code: "lt-LT", label: "Lietuvi" },
    { code: "lv-LV", label: "Latvieu" },
    { code: "my-MM", label: "Burmese" },
    { code: "nb-NO", label: "Norsk bokml" },
    { code: "nl-NL", label: "Nederlands" },
    { code: "nn-NO", label: "Norsk nynorsk" },
    { code: "oc-FR", label: "Occitan" },
    { code: "pa-IN", label: "" },
    { code: "pl-PL", label: "Polski" },
    { code: "pt-BR", label: "Portugus Brasileiro" },
    { code: "pt-PT", label: "Portugus" },
    { code: "ro-RO", label: "Romn" },
    { code: "ru-RU", label: "" },
    { code: "sk-SK", label: "Slovenina" },
    { code: "sv-SE", label: "Svenska" },
    { code: "sl-SI", label: "Slovenina" },
    { code: "tr-TR", label: "Trke" },
    { code: "uk-UA", label: "" },
    { code: "zh-CN", label: "" },
    { code: "zh-TW", label: "" },
    { code: "vi-VN", label: "Ting Vit" },
    { code: "mr-IN", label: "" },
  ]
    .filter(lang => {
      const completion = (percentages as Record<string, number>)[lang.code] ?? 0;
      return completion >= COMPLETION_THRESHOLD || EXTRA_LANGUAGE_CODES.has(lang.code);
    })
    .sort((left, right) => (left.label > right.label ? 1 : -1)),
];

let currentLang: Language = defaultLang;
let currentLangData = {};

export const setLanguage = async (lang: Language) => {
  currentLang = lang;
  document.documentElement.dir = currentLang.rtl ? "rtl" : "ltr";
  document.documentElement.lang = currentLang.code;

  try {
    currentLangData = await import(`./locales/${currentLang.code}.json`);
  } catch (error: any) {
    console.error(`Failed to load language ${lang.code}:`, error.message);
    currentLangData = fallbackLangData;
  }

  editorJotaiStore.set(editorLangCodeAtom, lang.code);
};

export const getLanguage = () => currentLang;

const findPartsForData = (data: any, parts: string[]) => {
  for (let index = 0; index < parts.length; ++index) {
    const part = parts[index];
    if (data[part] === undefined) {
      return undefined;
    }
    data = data[part];
  }
  if (typeof data !== "string") {
    return undefined;
  }
  return data;
};

export const t = (
  path: NestedKeyOf<typeof fallbackLangData>,
  replacement?: { [key: string]: string | number } | null,
  fallback?: string
) => {
  const parts = path.split(".");
  let translation =
    findPartsForData(currentLangData, parts) ||
    findPartsForData(fallbackLangData, parts) ||
    fallback;
  if (translation === undefined) {
    const errorMessage = `Can't find translation for ${path}`;
    // in production, don't blow up the app on a missing translation key
    if (import.meta.env.PROD) {
      console.warn(errorMessage);
      return "";
    }
    throw new Error(errorMessage);
  }

  if (replacement) {
    for (const key in replacement) {
      translation = translation.replace(`{{${key}}}`, String(replacement[key]));
    }
  }
  return translation;
};

/** @private atom used solely to rerender components using `useI18n` hook */
const editorLangCodeAtom = atom(defaultLang.code);

// Should be used in components that fall under these cases:
// - component is rendered as an <JsonDraw> child
// - component is rendered internally by <JsonDraw>, but the component
//   is memoized w/o being updated on `langCode`, `AppState`, or `UIAppState`
export const useI18n = () => {
  const langCode = useAtomValue(editorLangCodeAtom);
  return { t, langCode };
};
